import React from 'react';
import './social.scss';

function Social_Icon() {
    return (
        <div className="social_icon">
            <ul>
                <li><a href="#">  <i className=" text-black-50 fab fa-facebook    "></i> </a></li>
                <li><a href="#"> <i className=" text-black-50 fab fa-twitter    "></i> </a></li>
                <li><a href="#">  <i className=" text-black-50 fab fa-instagram    "></i></a></li>
                <li><a href="#"> <i className=" text-black-50 fab fa-youtube    "></i> </a></li>
                <li><a href="#"> <i className=" text-black-50 fab fa-pinterest    "></i></a></li>
            </ul>
        </div>
    )
}

export default Social_Icon;
