import React from 'react';
import { useEffect } from 'react';
import './section3.scss';
import Swiper from 'swiper';

function HomeSection3() {
    useEffect(() => {
        var swiper = new Swiper('.swiper_container2', {
            effect: 'coverflow',
            grabCursor: true,
            centeredSlides: true,
            slidesPerView: 'auto',
            initialSlide: 4,
            coverflowEffect: {
                rotate: 50,
                stretch: 0,
                depth: 100,
                modifier: 1,
                slideShadows: true,
            },
            pagination: {
                el: '.swiper-pagination',
            },
            breakpoints: {
                640: {
                  slidesPerView: 2,
                  spaceBetween: 5,
                },
                768: {
                  slidesPerView: 3,
                  spaceBetween: 5,
                },
                1024: {
                  slidesPerView: 3,
                  spaceBetween: 10,
                },
              }

        });
    });
    return (
        <section className="HomeSection3 container-fluid">
            <h3 className="text-center py-4">
                TOP INFLUENCER MODELS
         </h3>
            <div className="row">
                <div className="swiper-container swiper_container2">
                    <div className="swiper-wrapper">
                        <div  className="swiper-slide" ></div>
                        <div  className="swiper-slide" ></div>
                        <div  className="swiper-slide"></div>
                        <div  className="swiper-slide"></div>
                        <div  className="swiper-slide"></div>
                        <div  className="swiper-slide"></div>
                        <div  className="swiper-slide"></div>
                        <div  className="swiper-slide"></div>
                        <div  className="swiper-slide"></div>
                        <div  className="swiper-slide" ></div>
                    </div>
                    <div className="swiper-pagination"></div>
                </div>
            </div>
        </section>
    )
}

export default HomeSection3;
