import React, { useState, useEffect, useRef } from 'react';
import './header.scss'
function Header() {
    const [toggle, setToggle] = useState(false);
    const btn = useRef("");
    const menuDrop = useRef('');
    const docToggle = (e)=> {
        if(!btn.current.contains(e.target) && !menuDrop?.current.contains(e.target)){
            setToggle(false)
        }
    }


    useEffect(()=> {
        window.onscroll = ()=> {
            if(document.documentElement.scrollTop > 100){
                setToggle(false)
            } 
           } 

        document.addEventListener('mousedown', docToggle);

        return ()=> document.removeEventListener('mousedown', docToggle)
           
    })   
   
    
   

    return (
        <header className="container-fluid ">
            <nav className="row p-3 position-relative align-items-center justify-content-lg-start justify-content-between ">
                <div className="col-2 custom_logo">
                    <a href="#" className="logo_link">
                        <span>YO</span>
                        <span  className="color_pink">YO</span>
                        <span>M</span>
                        <span>O</span>
                        <span>D</span>
                        <span className="color_pink">E</span>
                        <span>L</span>

                    </a>
                </div>
                <div ref={menuDrop} className={toggle ?
                 'col-7 d-lg-block after_toggle_col sm_mobile_menu'
                 : 'col-7 before_toggle_col d-lg-block sm_mobile_menu'}>
                    <ul className="d-flex menu_sm">
                        <li><a className="p-2 text-light menu_link " href="#"> Home </a></li>
                        <li><a className="p-2 text-light menu_link " href="#"> About </a></li>
                        <li><a className="p-2 text-light menu_link " href="#"> BMO Models </a></li>
                        <li><a className="p-2 text-light menu_link " href="#"> Services </a></li>
                        <li><a className="p-2 text-light menu_link " href="#"> Work with us </a></li>
                        <li><a className="p-2 text-light menu_link " href="#"> More </a></li>
                    </ul>
                </div>
                <div className=" text-light col-lg-3 col-8 col-sm-9 justify-content-end d-flex m-0 p-0">
                    <div className="shop px-2"> <a href="#"><i className="  fas fa-shopping-bag    "></i> Shop</a></div>
                    <div className="profile px-2"> <a href="#"><i className="  fas fa-user-circle    "></i> Profile</a></div>
                    <div className="login px-2"> <a href="#"><i className="  fas fa-sign-in-alt    "></i> Login</a></div>
                </div>
                <div className="  col-2 col-sm-1 d-block d-lg-none">
                    <button onClick={()=> setToggle(!toggle)} ref={btn} className="toggle_nav">
                        {toggle ? <i class="fas fa-times    "></i> : 
                        
                        <i className="fas fa-bars    "></i>
                        
                        }
                    </button>
                </div>
            </nav>
        </header>
    )
}

export default Header;
