import React, { useState } from 'react';
import './section1.scss';

function HomeSection1() {
    const [bgPosition, setbgPosition] = useState(0)
    const homeperMove = (e) => {
        setbgPosition(-e.clientX / 25 + 'px ' + e.clientY / 25 + 'px');
    }
    const homeperOut = () => {
        setbgPosition('center')
    }
    return (
        <section onMouseOut={homeperOut} style={{ backgroundPosition: bgPosition }} onMouseMove={homeperMove} className="homeSection1 container-fluid pt-5 pb-5">

            <h1 className="text-center text-light pt-5 mt-5">
                DISCOVER MODELS OR BE DISCOVERED
            </h1>
            <h2 className="text-center text-light">
                THE EASIER WAY TO FIND MODELS
            </h2>
            <div className="btn_wrapper text-center pb-5 mb-5">
                <a className=" py-2 text-light px-1 m-2 d-inline-block" href="#"> I'M LOOKING FOR MODEL JOBS </a>
                <a className=" py-2 text-light px-1 m-2 d-inline-block" href="#"> I'M LOOKING FOR MODELS </a>
            </div>

        </section>
    )
}

export default HomeSection1;
