import React, { useState, useEffect } from 'react';
import './section2.scss';
import Swiper from 'swiper';
import 'swiper/css/swiper.min.css'

function HomeSection2() {
  useEffect(() => {

    new Swiper('.swiper_container1', {
      breakpoints: {
        640: {
          slidesPerView: 2,
          spaceBetween: 10,
        },
        768: {
          slidesPerView: 3,
          spaceBetween: 15,
        },
        1024: {
          slidesPerView: 4,
          spaceBetween: 20,
        },
      }
    });


  });
  return (
    <section className="HomeSection2 container-fluid">
      <h3 className="text-center  py-4">
        MODELING FOR EVERYONE

        </h3>
      <div className="swiper-container swiper_container1">
        <div className="swiper-wrapper">
          <div data-aos="zoom-in" className="swiper-slide position-relative"><img src="https://cdn.pixabay.com/photo/2015/11/26/00/14/fashion-1063100__340.jpg" alt="" />
            <div className="name position-absolute">Liza Ape</div></div>
          <div data-aos="zoom-in" className="swiper-slide position-relative"><img src="https://cdn.pixabay.com/photo/2015/01/15/12/46/model-600225__340.jpg" alt="" />

                        .name<div className="name position-absolute">Aadhya</div></div>
          <div data-aos="zoom-in" className="swiper-slide position-relative"><img src="https://cdn.pixabay.com/photo/2017/03/30/18/17/girl-2189247__340.jpg" alt="" />

                        .name<div className="name position-absolute">Aahana</div></div>
          <div data-aos="zoom-in" className="swiper-slide position-relative"><img src="https://cdn.pixabay.com/photo/2016/01/19/17/19/young-woman-1149643__340.jpg" alt="" />

                        .name<div className="name position-absolute">Aalia</div></div>
          <div data-aos="zoom-in" className="swiper-slide position-relative"><img src="https://cdn.pixabay.com/photo/2016/11/22/06/05/girl-1848454__340.jpg" alt="" />

                        .name<div className="name position-absolute">Aanya</div></div>
          <div data-aos="zoom-in" className="swiper-slide position-relative"><img src="https://cdn.pixabay.com/photo/2017/03/05/23/14/girl-2120196__340.jpg" alt="" />

                        .name<div className="name position-absolute">Emma</div></div>
          <div data-aos="zoom-in" className="swiper-slide position-relative"><img src="https://cdn.pixabay.com/photo/2017/08/06/15/13/woman-2593366__340.jpg" alt="" />

                        .name<div className="name position-absolute">Isabella</div></div>
        </div>
      </div>

    </section>
  )
}

export default HomeSection2;
