import React, { useEffect, useState } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import './App.scss'; // main global style;
import Header from './component/header/header';
import Footer from './component/footer/footer';
import Home from './component/home/home';
import Music from './component/media/music.mp3'


// ........ AOS Scroll Library 

import AOS from 'aos';
import 'aos/dist/aos.css';

// ......... Particles Library
import Particles from 'react-particles-js';

// .......... Loading Page ............... 
import Loading from './component/loading/loading';

// ............. ParallaxSide ................
import ParallaxSide from './component/parallaxSide/parallaxSide';

function App() {
  const [loading, setLoading] = useState(false);
  useEffect(() => {
    AOS.init();
    let pro = new Promise((resolve, reject) => {
      let music = new Audio();
      music.src = Music;
      music.play();
      music.volume = .5;
      music.loop = true;
      resolve('Success');
      console.log('Music Loading Now..');
    })
    pro.then((successmsg) => {
      console.log('I am successfull to learn Promise by ' + successmsg)
    });

    
    setLoading(true);
  })



  return (
    <React.Fragment>
      {loading ?
        <>

          <Particles className="particles" />
          <ParallaxSide/>

          <Header />
          <Home />
          <Footer />



        </>
        : <Loading />

      }
    </React.Fragment>
  );
}

export default App;
